@media (min-width: 1000px) {
  .container-slide {
    display: grid;
    margin: 0 auto;
    top: 20px;
    position: relative;
    width: 80%;
    grid-template-columns: 1fr 1fr;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
  }
  .container-slide-mobile {
    display: none;
  }

  .imagem img {
    width: 100%;

    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
  }

  .title-slide {
    font-size: 3rem;
    font-weight: 400;
    color: #194d4d;
  }

  .text-slide {
    margin-top: 50px;
    font-size: 1.2vw;
  }

  .button-slide {
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 140px;
    bottom: 30px;
    position: absolute;
    border-radius: 5px;
    border: 1px solid;
  }

  .texto {
    padding: 5px 12px;
    border-top-left-radius: 10px;
    width: 450px;
    border-bottom-left-radius: 10px;
  }

  .left-arrow {
    width: 30px;
    opacity: 50%;
    transition: 0.3s;

    cursor: pointer;
  }
  .right-arrow {
    opacity: 50%;
    width: 30px;
    transition: 0.3s;
    transform: rotate(180deg);
    cursor: pointer;
  }

  .left-arrow:hover,
  .right-arrow:hover {
    opacity: 1;
  }

  .arrow {
    margin: 0 auto;
    display: flex;
    align-items: center;
    margin-top: 30px;
    justify-content: space-between;
    width: 10%;
  }
}
@media (max-width: 1000px) {
  body {
    background-color: #f5f5f5;
  }
  .container-web {
    display: none;
  }
  .arrow {
    display: none;
  }

  .container-slide-mobile {
    margin: 0 auto;
  }
  .image {
    height: 400px;
    background-size: cover;
    background-position: center;

    background-image: url(../Assets/familymobile.jpg);
  }

  .content-mobile {
    height: 100px;
    border-radius: 10px;
    margin: 0 auto;
    width: 40%;
    height: 200px;
    position: relative;
    padding: 20px;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    margin-top: -100px;
    background-color: rgb(255, 255, 255);
  }
  .title-slide {
    font-size: 2rem;
    color: #194d4d;
  }
  .text-slide {
    margin-top: 20px;
  }
  .button-slide {
    position: absolute;
    bottom: 10px;
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 140px;
    bottom: 30px;
    border-radius: 5px;
    border: 1px solid;
  }
}
@media (max-width: 800px) {
  .content-mobile {
    height: 260px;
    width: 260px;
  }
  .image {
    height: 400px;
    background-size: cover;
    background-position: center;
    background-image: url(../Assets/familymobile.jpg);
  }
  .button-slide {
    bottom: 10px;
    padding: 10px;
    display: flex;
    margin: 0 auto;
    justify-content: center;
    align-items: center;
    width: 140px;
    bottom: 30px;
    color: rgb(255, 255, 255);
    border-radius: 5px;
    background-color: #0b807b;
  }
}
