@media (min-width: 800px) {
  .consulta {
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-top: 20px;
    width: 80%;
  }

  .divisor {
    margin-top: 80px;
  }

  .imagem {
    width: 80%;
  }

  .button {
    border-radius: 5px;
    display: grid;
    grid-template-columns: 1fr 4fr;
    align-items: center;
    justify-content: center;
    color: #206666;
    margin-top: 20px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    height: 50px;
    font-size: 20px;
    width: 200px;
  }

  .button:hover {
    border-left: 4px solid;

    transition: 0.1s;
  }
  .button2:hover {
    outline: none;
    box-shadow: 0 0 0 3px #fea, 0 0 0 4px #fb1;
  }
  .button2 {
    border-radius: 5px;
    display: grid;
    grid-template-columns: 1fr 4fr;
    align-items: center;
    justify-content: center;
    color: #206666;
    background-color: #ffd47e;
    margin-top: 30px;
    height: 50px;
    font-size: 20px;
    width: 200px;
  }
  .button3 {
    border-radius: 5px;
    display: grid;
    grid-template-columns: 1fr 4fr;
    align-items: center;
    justify-content: center;
    color: #206666;
    margin-top: 30px;
    height: 50px;
    font-size: 20px;
    width: 200px;
  }
  .logos {
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: center;
  }
  .logos img {
    width: 20px;
  }

  .box {
    display: grid;
    margin-top: 50px;
    justify-items: center;
    grid-template-columns: 1fr 1fr;
  }

  .titlemargin {
    margin: 0 auto;
    width: 260px;
    display: block;
    height: 20px;
  }
}

@media (max-width: 800px) {
  .consulta {
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr;
    margin-top: 20px;
    width: 80%;
  }
  .imagem {
    display: none;
  }

  .divisor {
    margin-top: 80px;
  }

  .button {
    border-radius: 5px;
    display: grid;
    grid-template-columns: 1fr 4fr;
    align-items: center;
    justify-content: center;
    color: #206666;
    margin-top: 20px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    height: 50px;
    font-size: 20px;
    width: 220px;
  }
  .logos {
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: center;
  }
  .logos img {
    width: 20px;
  }

  .box {
    display: grid;
    margin-top: 50px;
    justify-items: center;
    grid-template-columns: 1fr;
  }
  .button2 {
    border-radius: 5px;
    display: grid;
    grid-template-columns: 1fr 4fr;
    align-items: center;
    justify-content: center;
    color: #206666;
    background-color: #ffd47e;
    margin-top: 30px;
    height: 50px;
    font-size: 20px;
    width: 200px;
  }
  .button3 {
    border-radius: 5px;
    display: grid;
    grid-template-columns: 1fr 4fr;
    align-items: center;
    justify-content: center;
    color: #206666;
    margin-top: 30px;
    height: 50px;
    font-size: 20px;
    width: 200px;
  }
  .titlemargin {
    margin: 0 auto;
    width: 220px;
    display: block;
    height: 20px;
  }
}
