@media (min-width: 800px) {
  .noticias {
    margin: 0 auto;
    margin-top: 50px;
    width: 80%;
  }
  .mais {
    margin-left: 10%;
  }
  .title {
    font-family: var(--type-first);
    margin: 1rem 0;
    padding: 0;
    color: #105555;
    margin-top: 60px;
    margin-left: 10%;
    position: relative;
    font-size: 2.2rem;

    line-height: 1;
  }
  .title::after {
    height: 25px;
    z-index: -1;
    content: "";
    left: -5px;
    display: block;
    width: 25px;
    border-radius: 5px;
    position: absolute;
    top: 0;
    background-color: #ffd47e;
  }

  .image {
    border-radius: 5px;
    background-position: center;
    background-size: cover;
    width: 300px;
    height: 300px;
    background-color: aquamarine;
  }

  .containerImage {
    display: flex;
    margin-top: 30px;
    gap: 20px;
  }
}
@media (max-width: 800px) {
  .noticias {
    margin: 0 auto;
    margin-top: 10px;
    width: 80%;
    overflow-x: auto;
    overflow-y: hidden;
  }
  .mais {
    margin-left: 10%;
  }
  .title {
    font-family: var(--type-first);
    margin: 1rem 0;
    padding: 0;
    color: #105555;
    margin-top: 60px;
    margin-left: 10%;
    position: relative;
    font-size: 2.2rem;

    line-height: 1;
  }
  .title::after {
    height: 25px;
    z-index: -1;
    content: "";
    left: -5px;
    display: block;
    width: 25px;
    border-radius: 5px;
    position: absolute;
    top: 0;
    background-color: #ffd47e;
  }

  .image {
    border-radius: 5px;
    background-position: center;
    background-size: cover;
    width: 200px;

    height: 200px;
    background-color: aquamarine;
  }

  .containerImage {
    display: flex;
    margin: 0 auto;
    margin-top: 30px;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;

    width: 50rem;
    overflow-x: scroll;
    gap: 20px;
  }
}
