.header {
  height: 110px;
  position: fixed;
  align-items: center;
  padding: 0.5rem 5%;
  background-color: white;
  z-index: 100;
  top: 0;
  width: 100%;
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.1);
}
.mobile {
  visibility: hidden;
}
.mobileButton {
  display: none;
}
a {
  color: #2b6666;
}
a:hover {
  color: #143131;
}

.header nav {
  display: flex;
  align-items: center;
}

.header ul {
  display: flex;
  margin-left: 50px;
  align-items: center;
}

.header ul li {
  margin-left: 30px;
  cursor: pointer;
}

.header img {
  width: 200px;
}

.active {
  border-bottom: 2px solid #eeb789;
}

.modal {
  padding: 30px 5%;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
  position: fixed;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  background-color: #fdf4e7;
  z-index: 100;
  height: 150px;
  width: 100%;
}

.modal2 {
  background-color: rgb(255, 0, 0);
  position: fixed;
  height: 200px;
  margin-top: 110px;

  width: 100%;
}

.modal3 {
  background-color: rgb(134, 108, 108);
  position: fixed;
  height: 200px;
  margin-top: 110px;
  width: 100%;
}

.modal4 {
  background-color: rgb(54, 167, 101);
  position: fixed;
  height: 200px;
  margin-top: 110px;
  width: 100%;
}

.title {
  font-size: 1.5vw;
  color: #cf8320;
  display: flex;
  align-items: center;

  font-weight: 300;
}

.lis {
  margin-left: 10%;
  font-weight: 100;
  font-size: 1.5vw;
}

.lis li {
  margin-top: 6px;
}

.none {
  display: none;
}
@media (max-width: 800px) {
  .header nav {
    display: flex;
    width: 100%;
    background-color: white;
    justify-content: space-between;
  }
  .header ul {
    display: none;
  }
  .mobileButton {
    background: #eee;
    border-radius: 0.2rem;
    height: 40px;
    width: 40px;
    display: flex;
    color: #092b2b;
    align-items: center;
    justify-content: center;
    border: 1px solid transparent;
    transition: 0.1s;
    cursor: pointer;
    padding: 0px;
  }
  .mobileButton::after {
    content: "";
    display: block;
    width: 1.2rem;
    height: 2px;
    transition: 0.2s;
    border-radius: 2px;
    box-shadow: 0 6px currentColor, 0 -6px currentColor;
    background-color: currentColor;
  }

  .mobileButton:focus,
  .mobileButton:focus,
  .mobileButtonActive {
    outline: none;
    background: white;
    border-color: #092e2e;
    color: #092e2e;
  }

  .mobile {
    width: 100%;
    height: 500px;
    visibility: visible;
    padding: 0 0.5rem;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    position: fixed;
    z-index: 1000;
    overflow-y: auto;
    background-color: rgb(255, 255, 255);
  }

  .mobileButtonActive::after {
    transform: rotate(90deg);
    width: 4px;
    height: 4px;
    box-shadow: 0 8px currentColor, 0 -8px currentColor;
  }
  .title {
    font-size: 20px;
    color: #092e2e;
  }

  .mobile section {
    display: grid;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    grid-template-columns: 1fr 1.5fr;
    align-items: center;
    height: 200px;
  }
  .mobile section li {
    margin-top: 15px;
  }
}
