@media (min-width: 800px) {
  .beneficios {
    background-image: url(../../Assets/background.svg);
    margin-top: 50px;
    position: relative;
    height: 1500px;
  }

  .box {
    position: absolute;
    top: 150px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    left: 20px;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    height: 300px;
    background-color: #ffffff;
    width: 75%;
    border-radius: 10px;
  }

  .box2 {
    position: absolute;
    top: 600px;
    display: grid;
    margin: 0 auto;
    grid-template-columns: 1fr 1fr;
    right: 20px;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    height: 300px;

    background-color: #ffffff;
    width: 75%;
    border-radius: 10px;
  }

  .box3 {
    position: absolute;
    top: 1050px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    left: 20px;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;

    height: 300px;

    background-color: #ffffff;
    width: 75%;
    border-radius: 10px;
  }
  .images {
    background-image: url(../../Assets/tomografia-computadorizada.png);
    background-size: cover;
    box-shadow: rgba(0, 0, 0, 0.37) 0px 3px 8px;
    border-radius: 10px;
  }
  .images2 {
    background-image: url(../../Assets/uti.jpg);
    background-size: cover;
    box-shadow: rgba(0, 0, 0, 0.37) 0px 3px 8px;
    border-radius: 10px;
  }
  .images3 {
    background-image: url(../../Assets/maternidade.jpg);
    background-size: cover;
    background-position: center;
    box-shadow: rgba(0, 0, 0, 0.37) 0px 3px 8px;
    border-radius: 10px;
  }

  .content {
    padding: 20px;
  }

  h1 {
    font-size: 2.5vw;
    font-weight: 500;
    color: #174949;
    margin: 0 auto;
  }

  .content p {
    margin-top: 20px;
    font-size: 1rem;
    width: 90%;
  }

  .bike {
    position: absolute;
    width: 350px;
    bottom: 0;
    right: 0;
  }
}
