@media (min-width: 800px) {
  .perguntas {
    width: 80%;
    position: relative;
    top: 100px;
    margin: 0 auto;
  }
  .perguntas a {
    color: rgb(48, 48, 48);
    border-bottom: 1px solid;
  }

  .pergunta li {
    padding: 15px;
    cursor: pointer;
    margin-top: 30px;
    font-size: 1.3rem;
    width: 60%;
    border-radius: 5px;
    display: flex;
    align-items: center;
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
    justify-self: center;
  }

  .pergunta p {
    width: 60%;
    background-color: aliceblue;
    border-bottom: 1px solid;
    padding: 20px;
    line-height: 30px;
    color: rgb(116, 116, 116);
  }
  .none {
    display: none;
  }

  .active {
    display: block;
    background-color: rgb(183, 223, 255);
    transition: 0.3s;
  }

  .pergunta li::before {
    content: "";
    width: 20px;
    display: block;
    height: 20px;
    margin-right: 20px;
    background-size: cover;
    margin-left: 0.5rem;
    background-image: url(../../Assets//question.svg);
  }
}
