@media (min-width: 800px) {
  .subheader {
    display: none;
  }
}
@media (max-width: 800px) {
  .subheader {
    background-image: url(../../Assets/clouds1.svg);
    height: 200px;
    position: relative;
    top: 20px;
    text-align: center;
    display: grid;
  }
  .button {
    height: 70px;
    color: white;
    font-size: 1.8rem;
    width: 300px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    border-radius: 5px;
    background-color: #0b807b;
  }
  .paragraf {
    font-size: 1.3rem;
    color: #053a37;
  }
}
