@media (min-width: 800px) {
  .footer {
    box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px,
      rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
    position: relative;
    margin-top: 200px;
    padding: 30px 0px;
  }

  .footer nav {
    display: flex;
    margin: 0 auto;
    justify-content: space-between;
    width: 80%;
  }

  .copyright {
    position: absolute;
    margin: 0 auto;
    bottom: 20px;
  }

  .copyright a {
    margin-left: 20px;
  }

  .redes li {
    height: 50px;
    width: 50px;
    margin-top: 20px;
  }
  .central h2 {
    font-weight: 300;
  }

  .central li {
    margin-top: 10px;
  }
}
@media (max-width: 800px) {
  .footer {
    box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px,
      rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
    position: relative;
    margin-top: 200px;
    width: 100%;
    padding: 30px 0px;
  }
  section {
    margin-top: 30px;
  }

  .footer nav {
    display: block;
    margin: 0 auto;
    justify-content: space-between;
    width: 80%;
  }

  .copyright p a {
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    margin-top: 20px;
    bottom: 20px;
  }

  .copyright a {
    margin-left: 20px;
  }
  .redes ul {
    display: flex;
    justify-content: space-between;
    margin-bottom: 80px;
  }
  .redes li {
    height: 50px;
    width: 50px;
    margin-top: 20px;
  }
  .central h2 {
    font-weight: 300;
  }
}
