* {
  box-sizing: border-box;
}

body {
  margin: 0 auto;
  color: #092e2e;
  -webkit-tap-highlight-color: transparent;

  --type-first: "AmpleSoft Pro", Arial;
  --type-second: "Spectral", Georgia;
  font-family: var(--type-first);
}
a {
  text-decoration: none;
  -webkit-tap-highlight-color: transparent;
}

h1,
h2,
h3,
h4,
p {
  margin: 0;
}

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  display: block;
  max-width: 100%;
}

button,
input {
  display: block;
  font-size: 1rem;
  font-family: var(--type-first);
}

.container {
  margin: 0 auto;
  max-width: 70rem;
  height: 4rem;
}
.title {
  font-family: var(--type-first);
  margin: 1rem 0;
  color: #0b3b3b;
  position: relative;
  font-weight: 400;
  font-size: 2.5rem;
  line-height: 1;
}

.title::after {
  height: 25px;
  content: "";
  display: block;
  width: 25px;
  border-radius: 5px;
  position: absolute;
  bottom: 0;
  z-index: -1;
  background-color: #ffd47e;
}

@media (max-width: 800px) {
  .title {
    font-family: var(--type-first);
    margin: 1rem 0;
    padding: 0;
    color: #105555;
    position: relative;
    font-size: 2.2rem;
    line-height: 1;
  }
  .title::after {
    height: 25px;
    content: "";
    left: -5px;
    display: block;
    width: 25px;
    border-radius: 5px;
    position: absolute;
    top: 0;
    background-color: #ffd47e;
  }
}
