@media (min-width: 800px) {
  .ajudar {
    top: 20px;
    display: flex;
    gap: 50px;
    flex-wrap: wrap;
    position: relative;
  }

  .box {
    height: 240px;
    padding: 10px;
    position: relative;
    width: 240px;
    border: 1px solid rgba(0, 0, 0, 0.171);
    border-radius: 5px;
  }

  .box h4 {
    font-size: 1.5rem;
    font-weight: 300;
  }
  .box h3 {
    font-size: 1.5rem;
    font-weight: 300;
    bottom: 20px;
    position: absolute;
  }

  .box p {
    margin-top: 20px;
  }

  .container {
    width: 80%;
    margin: 0 auto;
  }

  .button {
    padding: 5px 40px;
    font-size: 1.3rem;
    position: absolute;
    bottom: 20px;
    border-radius: 5px;
    border: 1px solid rgba(0, 0, 0, 0.267);
  }
  .button2 {
    padding: 5px 40px;
    font-size: 1.3rem;
    background-color: #ffd47e;
    position: absolute;
    bottom: 20px;
    border-radius: 5px;
  }
}

@media (max-width: 800px) {
  .container {
    width: 100%;
    position: relative;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
    z-index: -100;
    height: 1290px;
    background-color: #f1feff;
    margin: 0 auto;
  }

  .ajudar {
    top: 20px;
    display: grid;
    align-items: center;
    gap: 30px;
    justify-items: center;
    grid-template-columns: 1fr;
    position: relative;
  }

  .box {
    padding: 10px;
    border: none;
    position: relative;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    width: 260px;
    background-color: white;
    border-radius: 5px;
  }
  .box h4 {
    font-size: 1.9rem;
    color: #135e5b;
    font-weight: 300;
  }
  .box h3 {
    font-size: 1.5rem;
    font-weight: 300;
    bottom: 20px;
  }

  .box p {
    margin-top: 20px;
    margin-bottom: 50px;
  }
  .button {
    padding: 5px 40px;
    font-size: 1.3rem;
    bottom: 20px;
    border-radius: 5px;
    border: 1px solid rgba(0, 0, 0, 0.267);
  }
  .button2 {
    padding: 5px 40px;
    font-size: 1.3rem;
    background-color: #ffd47e;
    bottom: 20px;
    border-radius: 5px;
  }
  .checked {
    top: 10px;
    right: 10px;
    position: absolute;
  }
  .checked img {
    width: 30px;
  }
  .titleCenter {
    margin: 0 auto;
    width: 300px;
  }
}
